import React from 'react'

const PunchNotAvailable = (props) => {
    const {openModel,handleOpenClose}=props
    return (
        <div className={`modal fade attendanceLog ${openModel ? 'show' : ''}`} id="attenLogs"
            tabIndex="-1"
            style={{ display: openModel ? 'block' : 'none', }}
            aria-hidden={!openModel}
        >
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content rounded-0 border-0">
                    <div className="modal-header border-0">
                        <h1 className="modal-title fs-5 fw-bold text-primary">
                            Attendance Correction
                        </h1>
                        <button
                            type="button"
                            className="btn-close"
                            // data-bs-dismiss="modal"
                            // aria-label="Close"
                            onClick={() => handleOpenClose()}
                        />
                    </div>
                    <div className="modal-body pt-0">
                        <div className="table-responsive tableDiv">
                            <p>
                                No Punches were found through the machine or glueple.
                                So, you are not eligible to apply for correction. Please contact your respective HR for updation
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PunchNotAvailable
