import React, { useEffect, useState } from "react";
import AttendanceIDetails from "../components/attendanceDashboard/AttendanceIDetails";
import AttendanceStatusCount from "../components/attendanceDashboard/AttendanceStatusCount";

import Calender from "../components/calendar/Calender";
import SelectLeaveTypeModal from "../components/modal/SelectLeaveTypeModal";
import { useDispatch, useSelector } from 'react-redux'
import { attendanceDashboardActions } from '../core/resource/actions';
import moment from 'moment'

const AttendanceDashboard = () => {

  const {
    getMonthlyAttendancePending,
    getMonthlyAttendanceRes,
    getMonthlyAttendanceError,

  } = useSelector(state => state.attendanceDashboard)

  const [currentDate, setCurrentDate] = useState(moment());
  const [attendanceDetails, setAttendanceDetails] = useState([])
  const [selectedCalenderData, setSelectedCalenderData] = useState('')
  const [isOpenSelectedLeaveType, setIsOpenSelectedLeaveType] = useState('')

  const dispatch = useDispatch()
  useEffect(() => {
    getCheckInCheckOutdata()
    getAttendanceDashboard()
  }, [])
  useEffect(() => {
    getAttendance()
    getAttendanceSummart()
  }, [currentDate])

  useEffect(() => {
    if (!getMonthlyAttendancePending) {
      if (getMonthlyAttendanceError) {
        return;
      }
      if (getMonthlyAttendanceRes) {
        if (getMonthlyAttendanceRes?.code === 200) {
          setAttendanceDetails(getMonthlyAttendanceRes.data)
        }
      }
    }
  }, [
    getMonthlyAttendanceRes,
    getMonthlyAttendanceError,
    getMonthlyAttendancePending
  ]);

  const getCheckInCheckOutdata = () => {
    dispatch(attendanceDashboardActions.getCheckInCheckOutRequest())
  }

  const getAttendance = () => {
    const data = {
      month: currentDate.format('YYYY-MM')
    }
    dispatch(attendanceDashboardActions.getMonthlyAttendanceRequest(data))
  }

  const getAttendanceSummart = () => {
    const data = {
      month: currentDate.format('YYYY-MM')
    }
    dispatch(attendanceDashboardActions.getAttendanceSummaryRequest(data))
  }
  const getAttendanceDashboard = () => {
    dispatch(attendanceDashboardActions.getAttendanceDashBoardRequest())
  }
  // useEffect(() => {
  //   if (selectedCalenderData) {
  //     setIsOpenSelectedLeaveType(!isOpenSelectedLeaveType)
  //   }
  // }, [selectedCalenderData])

  const LeaveTypeModal = (data) => {
    if(data){
      setSelectedCalenderData(data)
    }
    setIsOpenSelectedLeaveType(!isOpenSelectedLeaveType)
  }



  return (
    <>
      <AttendanceIDetails
        getCheckInCheckOutdata={getCheckInCheckOutdata} />
      <AttendanceStatusCount />
      <Calender
        setCurrentDate={setCurrentDate}
        currentDate={currentDate}
        attendanceDetails={attendanceDetails}
        handleOpenClose={LeaveTypeModal}
        // setSelectedCalenderData={setSelectedCalenderData}
      />
      {isOpenSelectedLeaveType &&
        <SelectLeaveTypeModal
          selectedCalenderData={selectedCalenderData}
          isOpen={isOpenSelectedLeaveType}
          handleOpenClose={LeaveTypeModal}
        />}
    </>
  );
};

export default AttendanceDashboard;
