import React, { useEffect, useState } from 'react'
import { CustomTable } from '../reuseableComponents';
import { attendanceCorrectionActions } from '../../core/resource/actions';
import { useDispatch, useSelector } from 'react-redux';
import { dateFormate } from '../../utils/common';


const AttendanceLogsModal = (props) => {
    const dispatch = useDispatch()
    const {
        isOpen,
        handleOpenClose,
        // attendanceLogsData,
        selectedData,
    } = props
    const [attendanceLogsData, setAttendanceLogsData] = useState([])
    const [currentPage, setCurrentPage] = useState()
    const {
        getAttendanceLogsPending,
        getAttendanceLogsRes,
        getAttendanceLogsError,

    } = useSelector(state => state.attendanceCorrection)

    const attendanceLogsFields = [
        { key: "sn", label: "S.N" },
        { key: "punch_time", label: "Time" },
        { key: "device_from_name", label: "Activity Name" },
        { key: "log_type", label: "IN/OUT" }
    ]
    const attendanceLogsScopedSlots = {
        sn: (_, index) => (
            <span className='srNum'>{index + 1}</span>
        ),

    };
    useEffect(() => {
        getAttendanceLogs()
    }, [selectedData])
    /* Get Attendance Logs */
    const getAttendanceLogs = () => {
        const payload = {
            date: selectedData?.attendance_date,
            emp_id: selectedData?.emp_id
        }
        dispatch(attendanceCorrectionActions.getAttendanceLogsRequest(payload))

    }
    useEffect(() => {
        if (!getAttendanceLogsPending) {
            if (getAttendanceLogsError) {
                return;
            }
            if (getAttendanceLogsRes) {
                if (getAttendanceLogsRes?.code === 200) {
                    setAttendanceLogsData(getAttendanceLogsRes?.data)
                }
            }
        }
    }, [
        getAttendanceLogsRes,
        getAttendanceLogsError,
        getAttendanceLogsPending
    ]);


    return (
        <>
            <div class="modal fade attendanceViewodal" id="viewModal" style={{ display: isOpen ? "unset !important" : "none !important", opacity: isOpen ? "unset" : "0" }}>
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content rounded-0 border-0">
                        <div class="modal-header border-0">
                            <h1 class="modal-title fs-5 fw-bold text-primary">Attendance Logs
                            </h1>
                            <button type="button"
                                class="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                                onClick={() => handleOpenClose()}
                            ></button>
                        </div>
                        <div class="modal-body">
                            <div class="d-flex align-items-center justify-content-between mb-3">
                                <strong class="fs-7 text-primary">
                                    <span class="text-light fw-normal">Date :</span>
                                    {dateFormate(selectedData?.attendance_date)}
                                </strong>
                                <strong class="fs-7 text-primary">{selectedData?.day_of_week}</strong>

                            </div>
                            <div class="timeCountmdl">
                                <strong>
                                    <span>{selectedData?.total_working_hours || "00:00:00"}</span>
                                    Working Hours
                                </strong>
                                <strong>
                                    <span>{selectedData?.total_break_time || "00:00:00"}</span>
                                    Break Time
                                </strong>
                            </div>
                            <div class="table-responsive attendanceTbl">
                                <CustomTable
                                    data={attendanceLogsData}
                                    fields={attendanceLogsFields}
                                    isLoading={getAttendanceLogsPending}
                                    pagination={true}
                                    currentPage={currentPage}
                                    setCurrentPage={setCurrentPage}
                                    totalPages={''}
                                    isHeader={false}
                                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                                    scopedSlots={attendanceLogsScopedSlots}
                                />
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            {isOpen && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AttendanceLogsModal
