import React, { useState, useEffect, useRef, useCallback } from 'react'
import Webcam from 'react-webcam';
import { FaceDetection } from '@mediapipe/face_detection';
import { Camera } from '@mediapipe/camera_utils';


const CheckInCheckOutModel = (props) => {
    const webcamRef = useRef(null);
    const cameraRef = useRef(null);
    const { checkInModel, setCheckInModel, checkInOutSave, attendanceType, checkInStatus } = props
    const [isCamera, setIsCamera] = useState(true);
    const [isWebCamAvailable, setIsWebCamAvailable] = useState(false);
    const [captureImage, setCaptureImage] = useState('');
    const [isOpen, setIsOpen] = useState(true)
    const [loader, setloader] = useState(true)
    const [faceDetected, setFaceDetected] = useState(false);

    const videoConstraints = {
        width: 1280,
        height: 720,
        facingMode: 'user',
    };


    useEffect(() => {
        checkMediaAndAccess();
    }, []);

    useEffect(() => {
        setCaptureImage(isWebCamAvailable ? '' : "no_cam");
    }, [isWebCamAvailable]);

    const checkMediaAndAccess = async () => {
        try {
            // Ensure navigator.mediaDevices exists
            if (!navigator.mediaDevices || !navigator.mediaDevices.enumerateDevices) {
                console.error("MediaDevices API is not supported in this browser.");
                setIsWebCamAvailable(false);
                setCaptureImage("no_cam");
                return;
            }
    
            // Check for available devices
            const devices = await navigator.mediaDevices.enumerateDevices();
            const hasVideoInput = devices.some(device => device.kind === "videoinput");
    
            if (hasVideoInput) {
                setIsWebCamAvailable(true);
                setCaptureImage("");
            } else {
                setIsWebCamAvailable(false);
                setCaptureImage("no_cam");
            }
    
            // Check for camera availability using getUserMedia
            if (navigator.mediaDevices.getUserMedia) {
                await navigator.mediaDevices.getUserMedia({ video: true })
                    .then(() => setIsCamera(true))
                    .catch(err => {
                        console.error("Error accessing webcam:", err);
                        setIsCamera(false);
                    });
            } else {
                // Fallback for older browsers
                const getMedia = navigator.getUserMedia ||
                    navigator.webkitGetUserMedia ||
                    navigator.mozGetUserMedia ||
                    navigator.msGetUserMedia;
    
                if (getMedia) {
                    getMedia(
                        { video: true },
                        () => setIsCamera(true),
                        err => {
                            console.error("Fallback error accessing webcam:", err);
                            setIsCamera(false);
                        }
                    );
                } else {
                    console.error("getUserMedia is not supported in this browser.");
                    setIsCamera(false);
                }
            }
        } catch (error) {
            console.error("An error occurred during media access:", error);
            setIsWebCamAvailable(false);
            setCaptureImage("no_cam");
            setIsCamera(false);
        }
    };
    

    // const checkMediaAndAccess = async() => {
    //    await navigator.mediaDevices.enumerateDevices().then(devices =>
    //         devices.forEach(device => {
    //             console.log({ device });
    //             if (device.kind === 'videoinput') {
    //                 setIsWebCamAvailable(true);
    //                 setCaptureImage('');
    //             }
    //         }),
    //     );

    //     navigator.getMedia =
    //         navigator.getUserMedia || // use the proper vendor prefix
    //         navigator.webkitGetUserMedia ||
    //         navigator.mozGetUserMedia ||
    //         navigator.msGetUserMedia;

    //     navigator.mediaDevices.getUserMedia(
    //         { video: true },
    //         function () {
    //             setIsCamera(true);
    //         },
    //         function (err) {
    //             setIsCamera(false);
    //             // toast.warning('webcam is not available');
    //         },
    //     );
    // };

    const capture = useCallback(() => {
        const imageSrc = webcamRef.current.getScreenshot();
        setCaptureImage(imageSrc);
    }, [webcamRef]);

    const checkInBtn = async () => {
        if (!captureImage && isWebCamAvailable) {
            //   toast.warning('Please capture image');
            return;
        }
        let location;
        navigator.geolocation.getCurrentPosition(function (position) {
            location = {
                latitude: position.coords.latitude,
                longitude: position.coords.longitude,
            };
            checkInOutSave(
                location,
                captureImage,
                attendanceType
            );
            setCaptureImage(isWebCamAvailable ? '' : 'no_cam');
        });
    };
    useEffect(() => {
            if (checkInModel && webcamRef.current) {
                const videoElement = webcamRef.current.video;
    
                // Create face detection model
                const faceDetectionModel = new FaceDetection({
                    locateFile: (file) => {
                        return `https://cdn.jsdelivr.net/npm/@mediapipe/face_detection/${file}`;
                    },
                });
    
                faceDetectionModel.setOptions({
                    model: 'short',
                    minDetectionConfidence: 0.5,
                });
    
                faceDetectionModel.onResults((results) => {
                    // Debugging
                    // console.log("Face Detection Results:", results);
                    if (results.detections.length > 0) {
                        setFaceDetected(true);
                    } else {
                        setFaceDetected(false);
                    }
                });
    
                const camera = new Camera(videoElement, {
                    onFrame: async () => {
                        await faceDetectionModel.send({ image: videoElement });
                    },
                    width: 1280,
                    height: 720,
                });
    
                camera.start();
                cameraRef.current = camera;
            } else {
                stopCamera();
            }
            return () => {
                stopCamera();
            };
    }, [checkInModel]);

    const stopCamera = () => {
        if (webcamRef.current) {
            const stream = webcamRef.current.video.srcObject;
            if (stream) {
                const tracks = stream.getTracks();
                tracks.forEach((track) => track.stop());
            }
        }

        if (cameraRef.current) {
            cameraRef.current.stop(); // Stop the MediaPipe camera
            cameraRef.current = null; // Clear reference
        }
    };

    const getBtnOptions = () => {
        const data = { label: 'Check In', color: 'success' };
        if (checkInStatus && attendanceType === 'attendance') {
            data.label = 'Check Out';
            data.color = 'danger';
        }
        if (!checkInStatus && captureImage && attendanceType === 'attendance') {
            data.label = 'Check In';
            data.color = 'success';
        }
        if (checkInStatus && attendanceType === 'short_break') {
            data.label = 'Take short break';
            data.color = 'danger';
        }
        if (!checkInStatus && captureImage && attendanceType === 'short_break') {
            data.label = 'End short break';
            data.color = 'success';
        }
        if (checkInStatus && attendanceType === 'long_break') {
            data.label = 'Take long break';
            data.color = 'danger';
        }
        if (!checkInStatus && captureImage && attendanceType === 'long_break') {
            data.label = 'End long break';
            data.color = 'success';
        }
        if (checkInStatus && attendanceType === 'other_break') {
            data.label = 'Take other break';
            data.color = 'danger';
        }
        if (!checkInStatus && captureImage && attendanceType === 'other_break') {
            data.label = 'End other break';
            data.color = 'danger';
        }
        return data;
    };
    const attBtnOptions = getBtnOptions();
    return (
        <div>
            <div className="modal fade attendanceViewodal" style={{ display: checkInModel ? "unset !important" : "none !important", opacity: checkInModel ? "unset" : "0" }} >
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content">
                        <div className="modal-header border-0 pb-0">
                            <h1 className="modal-title fs-5 fw-bold text-primary">{attBtnOptions.label}</h1>
                            <button type="button" className="btn-close" onClick={() => {setCheckInModel(!checkInModel)}}></button>
                        </div>
                        <div className="modal-body">
                            <div className="checkInModaltxt">
                                {!isWebCamAvailable && (
                                    <div className="row">
                                        <div className="row text-center mt-3">
                                            <div className="col-md-12 mb-5">
                                                <h3>Unable to detect your webcam</h3>
                                                <span>
                                                    Your request will be sent to your reporting manager for
                                                    approval.
                                                </span>
                                            </div>
                                            <div className="col-md-12">
                                                <button color={attBtnOptions.color} onClick={checkInBtn}>
                                                    Click to {attBtnOptions.label}{' '}
                                                    {loader && (
                                                        <i
                                                            className="fa fa-spinner fa-spin"
                                                            style={{ textAlignLast: 'center' }}
                                                        ></i>
                                                    )}
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                )}
                                {!captureImage && (<>

                                    {isOpen && (
                                        <>
                                            {isCamera == true ? (
                                                <Webcam
                                                    audio={false}
                                                    height={250}
                                                    ref={webcamRef}
                                                    screenshotFormat="image/jpeg"
                                                    width={400}
                                                    videoConstraints={videoConstraints}
                                                />) : (<div className="mt-5">
                                                    <img
                                                        src={'images/cameranotavailable.png'}
                                                        alt="User Image"
                                                        height="172"
                                                        width="280"
                                                    ></img>
                                                    <h3 className="mt-2">camera not available</h3>
                                                    <h5 className="mt-2">Login with your mobile</h5>
                                                </div>)}
                                        </>)}
                                </>)}
                                {captureImage && isCamera == true && (
                                    <div className="col-md">
                                        <div className="text-center" style={{ margin: 'auto' }}>
                                            <h5 align="center" style={{ color: '#6f4e37' }}>
                                                CAPTURE IMAGE
                                            </h5>
                                            <div className="mt-5">
                                                <img
                                                    src={
                                                        captureImage && captureImage !== "no_cam"
                                                            ? captureImage
                                                            : 'images/capture.jpeg'
                                                    }
                                                    alt="User Image"
                                                    height="220"
                                                    width="400"
                                                ></img>
                                            </div>
                                        </div>
                                    </div>
                                )}
                                <strong className="fs-6 d-block text-primary mb-1 text-center">NOTE</strong>
                                <p className="text-center">
                                    Please align with camera then press capture button below
                                </p>

                                <div className="text-center">
                                    {/* {faceDetected  && */}
                                        <>
                                            {/* <p className="text-center text-success">Face detected! You can now capture your image.</p> */}
                                            <button type="button"
                                                className="btn btn-primary gradientBg w-75 d-flex align-items-center justify-content-center mx-auto" onClick={captureImage ? () => setCaptureImage('') : capture}
                                                disabled={!faceDetected}>
                                                {!captureImage ? (
                                                    <img src="images/capture.svg" alt="" width="20" className="me-2" />
                                                ) : (
                                                    <i className="fa fa-repeat" aria-hidden="true"></i>
                                                )}
                                            </button>
                                        </>
                                    
                                         {/* <p className="text-center text-danger">No face detected. Please align with the camera.</p> */}
                                    {/* } */}

                                    {captureImage &&
                                        <button type="button"
                                            className="btn btn-primary bg-success w-75 mt-2  d-flex align-items-center justify-content-center  mx-auto " color={attBtnOptions.color}
                                            style={{ display: "none !important" }} onClick={checkInBtn}>
                                            <img src="images/checkIn-icon.svg" alt="" width="15" className="me-2" />{attBtnOptions.label}
                                        </button>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {checkInModel && <div className="modal-backdrop fade show"></div>}
        </div>
         
    )
}

export default CheckInCheckOutModel