import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from 'react-redux'
import { attendanceDashboardActions } from '../../core/resource/actions';
import CheckInCheckOutModel from "../modal/attendanceDashboard/CheckInCheckOutModel";
import ApexCharts from 'react-apexcharts';
import { subtractFromMomentDate, workHoursPercentages, addInMomentDate, currentDateAndTime,timeStringToDuration } from "../../utils/AppHelper"
import { CustomSpinner } from "../globalComponents";


const AttendanceIDetails = (props) => {
  const { getCheckInCheckOutdata } = props
  const dispatch = useDispatch()
  const { getCheckInCheckOutPending,
    getCheckInCheckOutRes,
    getCheckInCheckOutError,

    checkInCheckOutPending,
    checkInCheckOutRes,
    checkInCheckOutError,

    getAttendanceDashBoardPending,
    getAttendanceDashBoardRes,
    getAttendanceDashBoardError,
  } = useSelector(state => state.attendanceDashboard)
  const [getAttendanceLog, setAttendacenLog] = useState()
  const [checkInModel, setCheckInModel] = useState(false)
  const [checkInStatus, setCheckInStatus] = useState(false);
  const [attendanceType, setAttendanceType] = useState('attendance');
  const [timer, setTimer] = useState('00:00:00');
  const [shiftHours, setShiftHours] = useState("00:00:00")
  const [timeLeft, setTimeLeft] = useState("00:00:00")
  const [workHoursPercentage, setWorkHoursPercentage] = useState("00")
  const [monthlyCount, setMonthlyCount] = useState({})
  const [monthlyAttendanceDetails, setmonthlyAttendanceDetails] = useState({})
  
  

  const options = {
    chart: {
      type: 'radialBar',
      height: 300,
    },
    plotOptions: {
      radialBar: {
        hollow: {
          size: '70%',
        },
        track: {
          background: '#e7eefc',
        },
        dataLabels: {
          show: false,
          name: {
            show: false,
          },
          value: {
            fontSize: '22px',
            offsetY: -10,
            formatter: (val) => `${val}%`,
          },
          total: {
            show: false,
            label: 'in office',
            formatter: function () {
              return `${workHoursPercentage}%<br>in office<br>${workHoursPercentage < 50 ? 'POOR' : 'GOOD'
                }`;
            },
            style: {
              fontSize: '16px',
              color: '#000',
              fontWeight: 'bold',
              textAlign: 'center',
            },
          },
        },
      },
    },
    fill: {
      colors: ['#1D5A7F'], // Radial fill color
    },
    stroke: {
      lineCap: 'round',
    },
  };

  const series = [workHoursPercentage];

  // const [chartOptions, setChartOptions] = useState({
  //   chart: {
  //     type: 'donut',
  //   },
  //   labels: ['Worked Hours', 'Remaining Hours', 'Overtime Hours'],
  //   colors: ['#00E396', '#FEB019', '#FF4560'],
  //   plotOptions: {
  //     pie: {
  //       donut: {
  //         size: '80%',
  //         labels: {
  //           show: false,
  //           total: {
  //             show: true,
  //             label: '190hrs',
  //             formatter: () => '190hrs',
  //             fontSize: '20px',
  //           },
  //         },
  //       },
  //     },
  //   },
  //   legend: {
  //     show: false,
  //   },
  //   tooltip: {
  //     enabled: false,
  //   },
  // });
 
  
  
  // const [seriess] = useState(totalWorkingHoursInMinute);



  const CheckInCheckModel = () => {
    setCheckInModel(!checkInModel)
  }
  useEffect(() => {
    setTimeLeft(subtractFromMomentDate(shiftHours, 'HH:mm', timer, 'seconds', "HH:mm:ss"));
    setWorkHoursPercentage(workHoursPercentages(shiftHours, timer));
  }, [timer, shiftHours]);

  useEffect(() => {
    if (!getAttendanceDashBoardPending) {
      if (getAttendanceDashBoardError) {
        return;
      }
      if (getAttendanceDashBoardRes) {
        if (getAttendanceDashBoardRes?.code === 200) {
          setMonthlyCount(getAttendanceDashBoardRes?.data?.attendanceCount)
          setmonthlyAttendanceDetails(getAttendanceDashBoardRes?.data?.monthlyAttendanceDetails)

        }
      }
    }
  }, [
    getAttendanceDashBoardRes,
    getAttendanceDashBoardError,
    getAttendanceDashBoardPending
  ]);

  useEffect(() => {
    if (!getCheckInCheckOutPending) {
      if (getCheckInCheckOutError) {
        return;
      }
      if (getCheckInCheckOutRes) {
        if (getCheckInCheckOutRes?.data?.code === 200) {
          const checkInCheck = getCheckInCheckOutRes?.data?.data?.attendanceLog
          setAttendacenLog(checkInCheck)
          setCheckInStatus(checkInCheck?.log_type === 'IN')
          const {
            firstEntryTime,
            workHours,
            workMinutes,
            workSeconds,
            shift_full_day_working_hours,
          } = getCheckInCheckOutRes?.data?.data?.attendanceTime
          if (firstEntryTime) {
            let workTime = `${workHours}:${workMinutes}:${workSeconds}`;
            setShiftHours(shift_full_day_working_hours)
            const intervalFun = setInterval(() => {
              workTime = addInMomentDate(workTime, 'hh:mm:ss', 1, "seconds", "HH:mm:ss")
              setTimer(workTime);
            }, 1000);
            return () => clearInterval(intervalFun);
          }
        }
      }
    }
  }, [
    getCheckInCheckOutRes,
    getCheckInCheckOutError,
    getCheckInCheckOutPending
  ]);

  useEffect(() => {
    if (!checkInCheckOutPending) {
      if (checkInCheckOutError) {
        return;
      }
      if (checkInCheckOutRes) {
        if (checkInCheckOutRes?.data?.code === 200) {
          dispatch(attendanceDashboardActions.handleClearAttendanceDashboardStore("HANDLE_CLEAR_CHECK_IN_CHECK_OUT_STORE"))
          getCheckInCheckOutdata()
        }
      }
    }
  }, [
    checkInCheckOutRes,
    checkInCheckOutError,
    checkInCheckOutPending
  ]);
  
  const checkInOutSave = (location, captureImage, attendanceTypes) => {
    let data = {
      log_type: checkInStatus ? 'OUT' : 'IN',
      ip_address: "",
      punch_time: currentDateAndTime("YYYY-MM-DD HH:mm:ss"),
      latitude: location?.latitude,
      longitude: location?.longitude,
      emp_img: captureImage,
      device_from: "web",
      user_agent: navigator.userAgent,
      attendance_type: attendanceTypes
    }
    dispatch(attendanceDashboardActions.checkInCheckOutRequest(data))
    CheckInCheckModel()
  }
  const data = null;
  return (
    <div className="row px-md-1">
      <div className="col-md-4 px-md-2 mb-3">
        <div className="cardBox h-100 pt-2">
          <div className="cardHead d-flex justify-content-between align-items-center border-bottom pb-2 mb-3">
            <h3 className="mb-0 fs-7">Today</h3>
            <span className="badge text-bg-success py-2 px-3 rounded-5 presentStatus">
              {/* Present */}
            </span>
          </div>
          <div className="row">
            <div className="col-6">
              <div className="presentContent">
                <img
                  src="images/finger-print-icon.svg"
                  alt=""
                  width={25}
                  className="mb-2"
                />
                <p>You have not marked yourself as present today !</p>
                <strong className="timerStrong">
                  <span>Time Left:</span>
                  {timeLeft}
                </strong>
              </div>
            </div>
            <div className="col-6 ps-0">
              <div className="presentGrphDiv">

                <ApexCharts options={options} series={series} type="radialBar" height={300} />
                <div className="presnedGraphTxt">
                  <strong>{workHoursPercentage}%</strong>
                  <span>in office</span>
                  <p>{workHoursPercentage < 50 ? "POOR" : "GOOD"}</p>
                </div>
              </div>
            </div>
          </div>
          <button
            type="button"
            className="btn btn-primary gradientBg mt-2 w-100"
            // data-bs-toggle="modal"
            // data-bs-target="#checkInModal"
            onClick={() => CheckInCheckModel()}
          >
            {checkInStatus ? "Check Out" : "Check IN"}
          </button>
        </div>
      </div>
      <div className="col-md-4 px-md-2 mb-3">
        <div className="row px-md-1">
          <div className="col-6 px-md-2 mb-3">
            <div className="cardBox h-100 daysBox" style={{minHeight: '110px'}}>
              <div>
                <span className="d-block">Total Working Days</span>
                <strong className="d-block">{monthlyCount?.total_working_days}</strong>
              </div>
              <figure>
                <img width={20} src="images/days-icon.svg" alt="" />
              </figure>
            </div>
          </div>
          <div className="col-6 px-md-2 mb-3">
            <div className="cardBox h-100 daysBox" style={{minHeight: '110px'}}>
              <div>
                <span className="d-block">
                  Days <br />
                  Present
                </span>
                <strong className="d-block">{monthlyCount?.present_count}</strong>
              </div>
              <figure>
                <img width={20} src="images/leaves-icon.svg" alt="" />
              </figure>
            </div>
          </div>
          <div className="col-6 px-md-2">
            <div className="cardBox h-100 daysBox" style={{minHeight: '110px'}}>
              <div>
                <span className="d-block">
                  Days <br />
                  Absent
                </span>
                <strong className="d-block">{monthlyCount?.absent_count}</strong>
              </div>
              <figure>
                <img width={20} src="images/days-icon.svg" alt="" />
              </figure>
            </div>
          </div>
          <div className="col-6 px-md-2">
            <div className="cardBox h-100 daysBox" style={{minHeight: '110px'}}>
              <div>
                <span className="d-block">
                  Leave <br />
                  Taken
                </span>
                <strong className="d-block">{monthlyCount?.paid_count}</strong>
              </div>
              <figure>
                <img width={20} src="images/leaves-icon.svg" alt="" />
              </figure>
            </div>
          </div>
        </div>
        <div className="logInmain">
          <div className="row align-items-center">
            <div className="col-md-4 px-md-2 border-end">
              <div className="d-flex justify-content-between align-items-center logIn">
                <figure className="m-0">
                  <img
                    src="images/weblogin.svg"
                    alt=""
                    width={22}
                    height={22}
                  />
                </figure>
                <div className="text-end">
                  <span className="d-block">Web</span>
                  <strong className="d-block">{monthlyCount?.web_count}</strong>
                </div>
              </div>
            </div>
            <div className="col-md-4 px-md-2 border-end">
              <div className="d-flex justify-content-between align-items-center logIn">
                <figure className="m-0">
                  <img
                    src="images/mobilelogin.svg"
                    alt=""
                    width={22}
                    height={22}
                  />
                </figure>
                <div className="text-end">
                  <span className="d-block">Mobile</span>
                  <strong className="d-block">{monthlyCount?.mobile_count}</strong>
                </div>
              </div>
            </div>
            <div className="col-md-4 px-md-2">
              <div className="d-flex justify-content-between align-items-center logIn">
                <figure className="m-0">
                  <img
                    src="images/biometriclogin.svg"
                    alt=""
                    width={20}
                    height={20}
                  />
                </figure>
                <div className="text-end">
                  <span className="d-block">Biometric</span>
                  <strong className="d-block">{monthlyCount?.biometric_count}</strong>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="col-md-4 px-md-2 mb-3 monthlyCard">
        <div className="cardBox h-100 pt-2 ">
          <div className="cardHead border-bottom pb-2 mb-3">
            <h3 className="mb-0 fs-7 py-1">Monthly hourly Attendance</h3>
          </div>
          <div className="row mb-5 align-items-center">
            <div className="col-6">
              <ul className="hrsChartInfo">
                <li className="mb-3">
                  {monthlyAttendanceDetails?.totalWorkingHours} <span>Working hrs</span>
                </li>
                <li className="mb-3">
                  00:00:00  <span>C-OFF hrs</span>
                </li>
                <li className="mb-3">
                  {monthlyAttendanceDetails?.totalBreakHours}  <span>Break hrs</span>
                </li>
                <li className="mb-3">
                  00:00:00 <span>Other</span>
                </li>
              </ul>
            </div>
            <div className="col-6">
              <div className="presentGrphDiv">
                {/* <div id="hrsChart" style={{ width: "100%", height: "128px" }} /> */}
                {/* <ApexCharts 
                  options={chartOptions}
                  series={seriess}
                  type="donut"
                  width="160"
                /> */}
                <div className="presnedGraphTxt">
                  <strong>{monthlyAttendanceDetails?.totalWorkingHours}</strong>
                  <span>/{monthlyAttendanceDetails?.totalWorkingDuration}</span>
                </div>
              </div>
            </div>
          </div>
          <div className="pt-3  border-top d-flex align-items-center justify-content-between">
            <strong className="timerStrong">
              <span>Average Login</span>
              {monthlyAttendanceDetails?.averageWorkingHours}
              {/* {`${data?.data?.attendance_basic_details?.average_login_time.split(":")[0]}hrs ${data?.data?.attendance_basic_details?.average_login_time.split(":")[1]}m ${data?.data?.attendance_basic_details?.average_login_time.split(":")[2]}s`} */}

            </strong>
            <strong className="timerStrong">
              <span>Average Break hrs</span>
              {monthlyAttendanceDetails?.averageBreakHours}
              {/* 1hr 01m */}
            </strong>
          </div>
        </div>
      </div>
      {checkInModel && <CheckInCheckOutModel
        checkInModel={checkInModel}
        setCheckInModel={setCheckInModel}
        checkInOutSave={checkInOutSave}
        checkInStatus={checkInStatus}
        attendanceType={attendanceType}
      />}
      {checkInCheckOutPending && <CustomSpinner isShow={checkInCheckOutPending} size="lg" />}
    </div>
  );
};

export default AttendanceIDetails;
